import React, { useState } from 'react';
import './App.css';
import'./COMPONENT/Navbar'
import Navbar from './COMPONENT/Navbar';
import Textforms from './COMPONENT/Textforms';
import About from './COMPONENT/About';
import Alert from './COMPONENT/Alert';
import Contact from './COMPONENT/Contact';

import {
  BrowserRouter as Router,
  Route,
  Link,
  Routes
} from "react-router-dom";


function App() {
  const [mode,setDarkMode] = useState('white');
  const[alert,setAlert] = useState(null);
  const showAlert = (message,type)=>{
    setAlert({
      msg:message,
      type:type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }               
  const toggleMode=()=>{
    if(mode==='dark'){
      setDarkMode("white");
      document.body.style.backgroundColor='white';
      showAlert("Light mode has been enable","Success");
      document.title = "TextUtils - light Mode";
    }
    else{
      setDarkMode('dark');
      document.body.style.backgroundColor = '#042649';
      showAlert("Dark mode has been enable","Success");
      document.title = "TextUtils - Dark Mode";

      // setInterval(() => {
      //   document.title="TextUtils install Now";
      // }, 2000);
      // setInterval(() => {
      //   document.title ="Textutils is ammeging";
      // }, 1500);

    }
  }

 
  return (
    <>
    <Router>
   <Navbar title="Textutils" about="About" contact="Contact" mode ={mode}  toggleMode={toggleMode}/>
    <Alert alert={alert} />
   <div className = "container my-3">
    <Routes>
    <Route exact path="/about" element={<About />} />
    <Route exact path="/" element={<Textforms heading="Enter the text to analyze" showAlert={showAlert} mode={mode} />} />
    <Route exact path ="/contact" element={<Contact/>}/>
    </Routes>
    
   </div>
   </Router>
   
  </>
  );
}

export default App;
