import React ,{useState}from 'react'



export default function Textforms(props) {
  //using hook 
  const[text, setText] = useState('');
  const handUpClick = ()=>{
    // console.log("uppercase ");
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("Conveted to upper case","Success");
  }
  const handLwClick = () =>{
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("Converted to lower case ","Success")
  }

  // write text 
  const handelOnchange = (event) =>{
    // console.log("onchage");
    setText(event.target.value);
  }
 
  const handonClear = ()=>{
    setText("");
    props.showAlert("Clear all text ","Success");
  }
  const handonSentence = () =>{
    // siddhartha sankar ghosh . i am 
    let concatinate="";
    let newText = text.split('.');
    for(let i=0;i<newText.length;i++){
      let element1 = newText[i].trim().charAt(0).toUpperCase();
      let element2= newText[i].substring(1 , newText[i].length).toLowerCase();
      concatinate = concatinate+element1+element2+".";
    }
    setText(concatinate);
    props.showAlert("Convert on Sentence text","Success");
  }
  const handonCopy =()=>{
    let text = document.getElementById("mybox");
    text.select();
    navigator.clipboard.writeText(text.value);
    props.showAlert("Copy text","Success");
  }
  const handonRemoveExtra =()=>{
    let newText = text.split(/[  ]+/);
    setText(newText.join(" "));
    props.showAlert("Remove extra specess","Success");
  }
 

  return (
   <>   
      <div className="container" style={{color:props.mode==='dark'?'white':'#042649'}}>
        <h1 color='red'>{props.heading}</h1>
        <div className="mb-3">
        <textarea className="form-control" id="mybox" style={{backgroundColor: props.mode==='dark'?'gray':'white',color:props.mode==='dark'?'white':'#042649'}} value ={text} onChange={handelOnchange} rows="8"></textarea>
        </div>
        <button className="btn btn-primary mx-2 btn-primary:hover" onClick={handUpClick}>Convert to uppercase</button>
        <button className="btn btn-primary mx-2" onClick={handLwClick}>Convert to Lowercase</button>
        <button className="btn btn-primary mx-2" onClick={handonClear}>Clear Text</button>
        <button className="btn btn-primary mx-2" onClick={handonSentence}>Sentence Text</button>
        <button className="btn btn-primary mx-2" onClick={handonCopy}>Copy Text</button>
        <button className="btn btn-primary mx-2" onClick={handonRemoveExtra}>Remove Extra Spaces</button>

        
      </div>
      <div className="container  my-3" style={{color:props.mode==='dark'?'white':'#042649'}} >
      <h2>your text Summary </h2>
      {/* [siddharha amitav rosan] */}
      <p>words {text.length<=0?'0': text.split(" ").filter(str=> str!=="").length} and character {text.replace(/[ .]/g, "").length} </p>
      {/* <p>words {toggleWord()} and character {text.length} </p> */}
      <p> {text.length<=0?'0':(text.split(" ").filter(str=>str!=="").length) * 0.008} Minutes Read</p>
      <h2>Preview </h2>
      <p>{text.length>0?text:"Enter someting to the text box to preview here"}</p>

      </div>
   </>
  )
}
