import React,{useState} from 'react'

export default function About() {
    
    const[myStyle,setMyStyle] = useState({
        color:'white',
        backgroundColor:"black",
        border:"1px solid white"
        
    });

    console.log(myStyle.color);

   const[btnText2,setBtnText] = useState("Enable Light mode");
    

    const toggelStyle = ()=>{
        if(myStyle.color ==='white'){
            setMyStyle({
                color:"black",
                backgroundColor:"white"
            })
            setBtnText("Enable dark mode");
        }
        else{
            setMyStyle({
                color:"white",
                backgroundColor:"black",
                border:"1px solid white"
            })
            setBtnText("Enable Light mode");
        }
    }

    
  return (
    <>
    <div className="container" style={myStyle}>
            <h1 className='my-2'>About Us</h1>
                    <div className="accordion" id="accordionExample">
            <div className="accordion-item">
            <h2 className="accordion-header">
            <button className="accordion-button"style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                About
            </button>
            </h2>
            <div id="collapseOne" className="accordion-collapse collapse show" data-bs-parent="#accordionExample">
            <div className="accordion-body" style={myStyle}>
                <strong>TextUtils</strong>  is a powerful and easy-to-use web application designed to simplify text manipulation tasks. Whether you're a student, writer, developer, or content creator, TextUtils helps you streamline and optimize text-related operations, saving you time and effort.
            </div>
            </div>
        </div>
        <div className="accordion-item">
            <h2 className="accordion-header">
            <button className="accordion-button collapsed" style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
               Our Mission
            </button>
            </h2>
            <div id="collapseTwo" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body" style={myStyle}>
                 The primary goal of TextUtils is to offer an efficient platform where users can:
                Count characters, words, and sentences quickly.
                Format and transform text in various ways (such as converting text to uppercase, lowercase, and more).
                Remove unnecessary spaces or unwanted characters in an instant.
                We aim to enhance your productivity and help you avoid tedious, manual text-editing work.
            </div>
            </div>
        </div>
        <div className="accordion-item">
            <h2 className="accordion-header">
            <button className="accordion-button collapsed" style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                Features
            </button>
            </h2>
            <div id="collapseThree" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body" style={myStyle}>
            TextUtils comes with several built-in tools that are helpful for various tasks, such as:

            Word and Character Count: Quickly determine the number of words or characters in your text.
            Text Case Conversion: Change text from lowercase to uppercase, capitalize words, and more.
            Remove Extra Spaces: Easily clean up unnecessary spaces or line breaks from your text.
            Text Formatter: Apply various transformations to format your text the way you want.
            Text to Speech: Convert your text into speech, useful for content creators and language learners.
            </div>
            </div>
        </div>


    </div>

    <div className="container my-3">
     <button  type="button" onClick={toggelStyle} className="btn btn-primary" >{btnText2}</button>
    </div> 
    </div>

    <div className='container my-3  '>
    <img src="https://static.vecteezy.com/system/resources/previews/000/555/460/original/classic-modern-3d-bold-script-font-vector.jpg" class="rounded float-left mx-4" alt="External Image"height={200}/>
    <img src="https://cdn.textstudio.com/output/preset/preview/large/0/8/2/9/928_232c0.webp"  class="rounded float-end mx-4" alt="External Image"height={200}/>
    <img src="http://img.over-blog-kiwi.com/0/26/98/15/20140408/ob_b5e22d_1010-54-039.jpg"  class="rounded float-end mx-4" alt="External Image"height={200}/>
    </div>
    </>
  )
}
