import React, { Component } from 'react'
import { Link } from 'react-router-dom'
// import PropTypes from 'prop-types'


export default function Navbar(props) {
  return (
    <>
       {/* <nav className="navbar navbar-expand-sm bg-body-tertiary"> */}
       <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
       {/* <nav className="navbar bg-primary" data-bs-theme="dark"> */}
      <div className="container-fluid">
        {/* props title */}
      <a className="navbar-brand" href="/">{props.title}</a>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
            {/* props about */}
          <Link className="nav-link" to="/about">{props.about}</Link>
        </li>
        <li className="nav-item">
            {/* props contact */}
          <Link className="nav-link" to="/Contact">{props.contact}</Link>
        </li>
      </ul>
      {/* <form className="d-flex" role="search">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form> */}
          <div className={`form-check form-switch text-${props.mode==='white'?'dark':'white'} mx-4`}>
          <input className="form-check-input" onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault" style={{color:props.mode==='white'?'black':'white'}}>Enable Dark Mode</label>
        </div>


    </div>
    </div>
   </nav>
    </>
  )
}

